.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.active
{
  color: aliceblue;
  background-color:rgb(100, 177, 253);
  position: relative;
  box-shadow: 0px 0px 12px 0px inset rgba(0,0,0,0.6);
  
}
.active::after
{
  content: ">";
  font-weight: bolder;
  display: inline-block;
  position:absolute ;
  right: 5px ;
}
.pressed:active
{
  box-shadow: 0px 0px 12px 1px inset rgba(0,0,0,0.6);
  
}

@keyframes appear 
{
  from{
    opacity: 0;
  }  
  
  to {
    opacity: 1;
  }
}
